/**
 * Disclaimer.js
 * Disclaimer Page For PolicyPal
 *
 * @version 0.0.1
 * @author [Wilson](wilson@policypal.com)
 */

import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import {
  onPageInsurance,
  setStateInsurance,
  onPageMore,
  setStateMore,
  onPageBusiness,
  setStateBusiness,
} from '../../actions/navActions';

import './RewardTos.scss';

// Footer
import FooterContainer from '../../containers/FooterContainer';

import { hideNavFooterIfMobile } from '../../lib/helperFunctions';

class RewardTos extends Component {
  constructor(props) {
    super(props);
    // reset indication
    const resetInsurance = onPageInsurance.NONE;
    const resetMore = onPageMore.NONE;
    const resetBusiness = onPageBusiness.NONE;
    const {
      resetIndicatorInsurance,
      resetIndicatorMore,
      resetIndicatorBusiness,
    } = this.props;
    resetIndicatorInsurance(resetInsurance);
    resetIndicatorMore(resetMore);
    resetIndicatorBusiness(resetBusiness);
  }

  componentDidMount() {
    hideNavFooterIfMobile(this.props.location);
  }

  render() {
    window.scrollTo(0, 0);
    return (
      <div className="privacy-policy reward-tos">
        <div className="title-container">
          <div className="title">Rewards</div>
        </div>
        <div className="content">
          <div className="content-row">
            {/* Point 1 */}
            <ul>
              <li className="title">Daily Reward Challenge</li>
              <li className="title-desc">
                Perk up your mornings with our Daily Reward Challenge! As the saying ‘the early bird gets the worm’ goes, PolicyPal is rewarding P$ to users who log into our app between 5 to 10 in the morning.
              </li>
            </ul>
            {/* Point 2 */}
            <ul>
              <li className="title">How Do You Benefit?</li>
              <li className="title-desc">
                The more P$ you accumulate, the more money you save on the purchases with PolicyPal. P$ 1 is equivalent to SGD $1 and can be directly used for purchases in PolicyPal.
              </li>
            </ul>
            {/* Point 3 */}
            <ul>
              <li className="title">How it Works</li>
              <li className="title-desc">
                <b>1) </b>
                Log into the PolicyPal app between 5 am to 10 am (GMT+8) to claim your daily reward.
              </li>
              <li className="title-desc">
                <b>2) </b>
                Reward incentive is randomly generated, your reward can range from P$0.01 to P$5.00 per claim.
              </li>
              <li className="title-desc">
                <b>3) </b>
                Multiply your rewards by 10 times every Sunday for consecutive check-ins during the week.
              </li>
            </ul>
            {/* Point 4 */}
            <ul>
              <li className="title">Terms &amp; Conditions</li>
              <li className="title-desc">
                <b>1) </b>
                The reward given out is final and non-transferrable.
              </li>
              <li className="title-desc">
                <b>2) </b>
                The P$ rewarded is valid for 1 year. Any purchase via PolicyPal will extend the P$ by an additional 1 year from the date of purchase.
              </li>
              <li className="title-desc">
                <b>3) </b>
                PolicyPal reserves the right at any time to amend or vary these Terms &amp; Conditions and/or any of the instructions or explanations at its sole discretion without prior notice. Any such changes shall be binding and will take effect immediately upon such amendment, change or deletion.
              </li>
              <li className="title-desc">
                Click <Link href to="/cashback">here</Link> for more details on P$ and Cashback.
              </li>
            </ul>
            {/* Point 5 */}
            <ul>
              <li className="title">------------------------</li>
            </ul>
            <ul>
              <li className="title">FAQs</li>
            </ul>
            {/* Point 6 */}
            <ul>
              <li className="title">How do I benefit from this challenge?</li>
              <li className="title-desc">
                It only takes 10 seconds to log into the PolicyPal app and check-in on your daily reward. The more P$ you accumulate, the more money you save on the purchases with PolicyPal. Make full use of your P$ on your purchases with PolicyPal - be it a travel insurance policy, an endowment plan or other policies listed by our trusted insurance providers.
              </li>
            </ul>
            {/* Point 7 */}
            <ul>
              <li className="title">What can I use my P$ for?</li>
              <li className="title-desc">
                The P$ can be used directly for your PolicyPal purchases. PolicyPal products include travel insurance, endowment insurance, motor insurance, retirement insurance, life insurance, pet insurance, personal accident insurance and business insurance.
              </li>
              <li className="title-desc">
                Stay tuned to this page as we will be introducing a marketplace featuring our partners’ products and services where you can also use P$ to offset your purchases in the marketplace from our partners!
              </li>
            </ul>
            {/* Point 8 */}
            <ul>
              <li className="title">Can I qualify for the 10x bonus reward only on Sundays?</li>
              <li className="title-desc">
                Yes, PolicyPal only gives out the 10x bonus reward every Sunday. Daily check-in begins every Monday and ends every Sunday. Users who check in daily from Monday will qualify for Sunday bonus rewards that will be multiplied 10 times.
              </li>
            </ul>
            {/* Point 9 */}
            <ul>
              <li className="title">Can I withdraw my P$ for actual cash?</li>
              <li className="title-desc">
                No, you cannot convert your P$ for actual cash. P$ can only be used on the PolicyPal platform.
              </li>
            </ul>
            {/* Point 10 */}
            <ul>
              <li className="title">Do my P$ expire?</li>
              <li className="title-desc">
                Your P$ is only valid for a year. However, you can extend its validity period by another year with every purchase made with PolicyPal.
              </li>
            </ul>
            <ul>
              <li className="title">How else can I earn P$?</li>
              <li className="title-desc">
                You can participate in our <a href="https://blog.policypal.com/blog/tipsandhacks/be-a-policypal-star-ambassador-and-earn-5-bonus-credits/" target="__blank">Star Ambassador programme</a>, where you will receive 5% bonus credits when your referrals successfully used your promo code to make a purchase with PolicyPal. What’s more, your promo code is customisable - so be creative!
              </li>
              <li className="title-desc">
              Also, with every purchase you make with PolicyPal, you will also receive 5% cashback in P$. For example, for a policy that costs $100, you will receive P$5 (5% of $100) credited to your account.
              </li>
            </ul>
          </div>
        </div>
        <FooterContainer />
      </div>
    );
  }
}

RewardTos.defaultProps = {
  resetIndicatorInsurance: null,
  resetIndicatorMore: null,
  resetIndicatorBusiness: null,
};
RewardTos.propTypes = {
  resetIndicatorInsurance: PropTypes.func,
  resetIndicatorMore: PropTypes.func,
  resetIndicatorBusiness: PropTypes.func,
  location: PropTypes.object.isRequired,
};

const mapDispatchToProps = dispatch => ({
  resetIndicatorInsurance: (resetInsurance) => {
    dispatch(setStateInsurance(resetInsurance));
  },
  resetIndicatorMore: (resetMore) => {
    dispatch(setStateMore(resetMore));
  },
  resetIndicatorBusiness: (resetBusiness) => {
    dispatch(setStateBusiness(resetBusiness));
  },
});

export default connect(null, mapDispatchToProps)(RewardTos);

